<template>
  <v-row>
    <v-col
      cols="12"
      sm="6"
    >
      <base-material-card
        color="primary"
        inline
        icon="mdi-map-marker"
        :title="this.$t('nav.point_of_service')"
        class="px-5 py-3"
      >
        <v-col cols="12">
          <v-row v-if="manual">
            <v-col
              cols="12"
              md="12"
            >
              <!-- custom text-field to blend it in -->
              <div class="v-input theme--light v-text-field">
                <div class="v-input__control">
                  <div class="v-input__slot">
                    <gmap-autocomplete
                      ref="place_component"
                      v-model="place.formatted_address"
                      required
                      :select-first-on-enter="true"
                      placeholder="Address"
                      :rules="validate.text"
                      @keydown.native.enter.prevent
                      @place_changed="setPlace"
                    />
                  </div>
                </div>
              </div>
            </v-col>
          </v-row>
          <v-row v-if="manual">
            <v-col
              cols="12"
              md="12"
            >
              <v-text-field
                v-model="api.name"
                label="Locatie naam"
                :rules="validate.text"
                color="primary"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <v-select
                v-model="api.brand_type"
                :items="brand_types"
                :rules="validate.item"
                label="Type station"
              />
            </v-col>
          </v-row>
          <v-row v-if="manual">
            <v-col
              cols="12"
              md="12"
            >
              <v-select
                v-model="fuel_types_answer"
                :items="fuel_types"
                label="Type station"
                multiple
                item-text="product_type"
                item-value="product_code"
                :rules="validate.items"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="6"
            >
              <v-checkbox
                v-model="api.always_open"
                :label="`24 / 7 | Openings tijden`"
                color="primary"
              />
            </v-col>
            <v-col
              cols="12"
              md="6"
            >
              <v-checkbox
                v-model="api.truck_stop"
                :label="`Truck stop`"
                color="primary"
              />
            </v-col>
          </v-row>
        </v-col>

        <v-btn
          color="primary"
          @click="$emit('submit')"
        >
          {{ api.id ? 'Update' : 'Create' }}
        </v-btn>
      </base-material-card>
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <base-material-card
        color="primary"
        inline
        icon="mdi-map-marker"
        title="Coördinaten"
        class="px-5 py-3"
      >
        <v-row v-if="manual">
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              v-model="geo.latitude"
              label="Latitude"
              color="primary"
              :rules="validate.required"
            />
          </v-col>
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              v-model="geo.longitude"
              label="Longitude"
              color="primary"
              :rules="validate.required"
            />
          </v-col>
        </v-row>

        <gmap-map
          style="width: 100%; height: 350px;"
          :zoom="place ? 15 : 8"
          :center="position"
        >
          <gmap-marker
            v-if="place"
            ref="marker"
            :position="position"
            :draggable="manual"
            @dragend="getDraggedMarkerCoordinates"
          />
        </gmap-map>
      </base-material-card>
    </v-col>
  </v-row>
</template>

<script>

  import validation from '@/utils/validation'

  export default {
    name: 'Crud',
    data: () => ({
      validate: validation,
      place: {},
      center: {
        // Utrecht als eerst centreren
        lat: 52.092876,
        lng: 5.104480,
      },
      geo: {
        longitude: 5.104480,
        latitude: 52.092876,
      },
      brand_types: [
        { text: 'Own mobile', value: 'own_mobile_point_of_service' },
        { text: 'Own card', value: 'own_card_point_of_service' },
        { text: 'Card', value: 'card_point_of_service' },
      ],
      fuel_types: [],
      api: {
        name: '',
        always_open: '',
        truck_stop: '',
        brand_type: '',
        address: {},
      },
    }),
    computed: {
      fuel_types_answer: {
        get: function () {
          let data = []
          if (this.api.id) {
            this.api.fuel_pumps.forEach(function (fuelPump) {
              fuelPump.fuel_products.forEach(function (fuelProduct) {
                data.push(fuelProduct.product_code)
              })
            })
          }
          return data
        },
        set: function (data) {
          this.api.fuel_types = data
        },
      },
      manual: function () {
        return (!this.api.id) || (this.api.origin === 'manual')
      },
      street: function () {
        return this.getPlaceItem('route') || this.api.street
      },
      street_number: function () {
        return this.getPlaceItem('street_number') || this.api.street_number
      },
      zipcode: function () {
        return this.getPlaceItem('postal_code') || this.api.zipcode
      },
      city: function () {
        return this.getPlaceItem('administrative_area_level_2') || this.api.city
      },
      position: {
        get: function () {
          return {
            lat: parseFloat(this.geo.latitude),
            lng: parseFloat(this.geo.longitude),
          }
        },
        set: function (position) {
          this.geo.latitude = position.lat
          this.geo.longitude = position.lng
        },
      },
    },
    mounted: function () {
      this.$http.get(`/portal/fuel_products`)
        .then(
          (response) => {
            this.fuel_types = response.data
          }
        )
        .catch(
          () => {
            this.$router.push({ name: 'point_of_service_all' })
          }
        )
    },
    methods: {
      submit: function () {
        this.$parent.submit()
      },
      getPlaceItem: function (typeName) {
        if (!this.place.address_components) {
          return false
        }

        let item = this.place.address_components.find(child => {
          for (let i = 0; i < child.types.length; i++) {
            if (child.types[i] === typeName) {
              return true
            }
          }
          return false
        })

        return item ? item.long_name : false
      },
      setPlace: function (place) {
        this.place = place

        if (place.address_components[0].types[0] !== 'street_number') {
          this.$refs.place_component.$refs.input.setCustomValidity(true)
        } else {
          this.$refs.place_component.$refs.input.setCustomValidity('')
        }

        this.position = {
          lat: (this.place.geometry.location.lat().toString()),
          lng: (this.place.geometry.location.lng().toString()),
        }
      },
      setEditData: function (data) {
        this.place.formatted_address = `${data.address.address_line1}, ${data.address.zip_code} ${data.address.city}`

        this.position = {
          lat: (data.address.latitude.toString()),
          lng: (data.address.longitude.toString()),
        }

        this.api = data
      },
      getDraggedMarkerCoordinates: function (e) {
        this.position = {
          lat: e.latLng.lat(),
          lng: e.latLng.lng(),
        }
      },
    },
  }
</script>

